* {
	box-sizing: border-box;
}

/* Slideshow container */
.slideshow-container {
	max-width: 90%;
	position: relative;
	margin: auto;
}

/* Make the images invisible by default */
.Containers {
	display: none;
}

.Containers img {
	object-fit: cover;
}

/* forward & Back buttons */
.Back,
.forward {
	cursor: pointer;
	position: absolute;
	top: 48%;
	width: auto;
	margin-top: -23px;
	padding: 17px;
	color: grey;
	font-weight: bold;
	font-size: 19px;
	transition: 0.4s ease;
	border-radius: 0 5px 5px 0;
	user-select: none;
}

/* Place the "forward button" to the right */
.forward {
	right: 0;
	border-radius: 4px 0 0 4px;
}

/*when the user hovers,add a black background with some little opacity */
.Back:hover,
.forward:hover {
	background-color: rgba(0, 0, 0, 0.8);
}

/* Caption Info */
.Info {
	background-color: var(--dark);
	color: var(--primary);
	font-size: 16px;
	padding: 10px 14px;
	position: absolute;
	bottom: 1px;
	width: 100%;
	text-align: center;
}

/* Worknumbering (1/3 etc) */
.MessageInfo {
	color: #f2f2f3;
	font-size: 14px;
	padding: 10px 14px;
	position: absolute;
	top: 0;
}

/* The circles or bullets and indicators */
.dots {
	cursor: pointer;
	height: 16px;
	width: 16px;
	margin: 0 3px;
	background-color: #acc;
	border-radius: 50%;
	display: inline-block;
	transition: background-color 0.5s ease;
}

.enable,
.dots:hover {
	background-color: #717161;
}

/* Faint animation */
.fade {
	-webkit-animation-name: fade;
	-webkit-animation-duration: 1.4s;
	animation-name: fade;
	animation-duration: 1.4s;
}

@-webkit-keyframes fade {
	from {
		opacity: 0.5;
	}
	to {
		opacity: 2;
	}
}

@keyframes fade {
	from {
		opacity: 0.5;
	}
	to {
		opacity: 2;
	}
}
